<template>
  <!-- 第一种导航 -->
  <div class="box-nav">
    <div class="nav-box-pc">
      <div class="content">
        <!-- logo -->
        <div class="logo">
          <a>
            <span>三寸人间</span>
          </a>
        </div>
        <!-- nav -->
        <div class="nav-right">
          <ul class="nav">
            <li class="nav-item" v-for="(i,index) in navItem" :key="i.id">
              <a @click.prevent="selectItem(index,i.url)">
                <span>{{i.name}}</span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navItem: [
        { name: "首页", url: "/home" },
        { name: "文章", url: "/article" },
        { name: "相册", url: "/photo" },
        // { name: "留言", url: "/message" },
        { name: "关于", url: "/about" }
      ]
    };
  },
  methods: {
    selectItem(index, url) {
      $(".nav-item a").removeClass("active");
      $(".nav-item a")
        .eq(index)
        .addClass("active");
      this.$router.push(url);
      if (window.sessionStorage) {
        window.sessionStorage.setItem("url", url);
        window.sessionStorage.setItem("index", index);
      }
    }
  },
  mounted() {
    $(".nav-item a")
      .eq(0)
      .addClass("active");
    if (window.sessionStorage) {
      let url = window.sessionStorage.getItem("url");
      let index = window.sessionStorage.getItem("index");
      if (url && index) {
        this.$router.push(url);
        $(".nav-item a").removeClass("active");
        $(".nav-item a")
          .eq(index)
          .addClass("active");
      }
    }
  },
  destroyed() {
    window.sessionStorage.removeItem("url");
    window.sessionStorage.removeItem("index");
  }
};
</script>

<style lang="scss" src="@/assets/scss/header.scss" scoped>
</style>


